<template>
  <div id="app">
    <header class="cdn-header">
      前端开源项目依赖
    </header>
    <div class="cdn-back">
      <div class="cdn-content">
        <div class="cdn-content-list" v-for="v in data" @click="dialog(v)">
          <span>{{v.name}}</span>
          <span>{{v.desc}}</span>
        </div>
      </div>
    </div>
    <at-modal v-model="modal">
        <p>{{dialogContent.name}}</p>
        <div slot="footer">
          <template v-for="(v,inx) in dialogContent.data">
            <at-input :readonly="true" v-model="v.url"  append-button>
                <template slot="append">
                    <span  :key="inx"  v-clipboard="copyData[inx]" @success="success">复制标签</span>
                </template>
            </at-input>
          </template>
        </div>
    </at-modal>
  </div>
</template>

<script>
import json from '../lib.json'
export default {
  name: 'app',
  watch: {
    'dialogContent.data' (v) {
      if (v.length) {
        v.forEach((vv, key) => {
          const arr = vv['url'].split('.')
          const ext = arr[arr.length - 1]
          /* eslint-disable no-useless-escape */
          const res = ext === 'js' ? `<script type=text/javascript src="${vv['url']}"><\/script>` : `<link href="${vv['url']}" rel=stylesheet>`
          this.$set(this.copyData, key, res)
        })
      }
    }
  },
  data () {
    return {
      copyData: {},
      dialogContent: {
        name: '',
        data: ''
      },
      modal: false,
      data: json
    }
  },
  methods: {
    success (res) {
      this.$Message.success('复制成功')
    },
    dialog (val) {
      this.dialogContent.name = val.name
      this.dialogContent.data = val.list
      this.modal = true
    },
    copy (url) {
      this.dialogContent.copy = url
    }
  }
}
</script>

<style>
#app {
  width: 100%;
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  text-align: center;
}
.cdn-header{
  width: 100%;
  padding: 100px;
  background: #8CBEB2;
  color: #5c4b51;
  font-size: 36px;
}

.cdn-back{
  width: 100%;
  background: #ffffff;
  height: 100%;
}

.cdn-content{
  width: 80%;
  margin: auto;
}

.cdn-content-list{
  border: 1px solid #e2ecf4;
  font-weight: bold;
  font-size: 20px;
  cursor: pointer;
  border-bottom:0;
  padding:10px;
}
.cdn-content-list:last-child{
  border-bottom: 1px solid #e2ecf4;
}
span:nth-child(1){
  float: left;
}
span:nth-child(2){
  font-weight: normal;
  font-size: 16px;
}

</style>



